<template>
  <el-row :gutter="20">
    <el-col :span="18" :offset="3">
      <p style="margin-bottom: 50px;line-height: 150%;">
        <a href="https://github.com/zhufuyi/sponge" target="_blank">sponge</a>
        is a powerful automatic code generation tool , is based on gin and grpc
        encapsulated microservices framework , is a common basic development
        framework. sponge has a rich generation of code commands to generate
        different functional code can be combined into a complete service (
        similar to the artificial dispersion of sponge cells can be
        automatically reorganized into a new sponge). Code decoupling modular
        design , it is easy to build a complete project from development to
        deployment , so you can develop web or microservices project easily,
        golang can also be "low-code development".
      </p>
      <p>
        github:
        <a href="https://github.com/zhufuyi/sponge" target="_blank"
          >https://github.com/zhufuyi/sponge</a
        >
      </p>
      <p>
        documentation:
        <a href="https://go-sponge.com" target="_blank"
          >https://go-sponge.com</a
        >
      </p>
      <br />
      <p>sponge generates code framework diagrams.</p>
      <div class="home-image">
        <el-image :src="homeImageSrc"></el-image>
      </div>
      <br />
      <p>Microservice service framework diagram.</p>
      <div class="microservices-image">
        <el-image :src="mircoServiceFrameworkImageSrc"></el-image>
      </div>
      <br />
      <p></p>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      homeImageSrc: require("@/assets/sponge-framework.png"),
      mircoServiceFrameworkImageSrc: require("@/assets/microservices-framework.png"),
    };
  },
};
</script>

<style>
.home-image {
  margin: auto;
}

.microservices-image {
  width: 70%;
  margin: auto;
}
</style>



