<template>
  <el-row :gutter="20">
    <el-col :span="spanSize" :offset="offsetSize">
      <div class="title">
        <h3>
          <span style="color: #e403a4"
            >Web services created based on protobuf</span
          >{{ title }}
        </h3>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="25%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="proto file" prop="protobufFile">
            <el-link
              style="margin-left: 10%;padding-bottom: 5px; font-size: 16px;"
              type="primary"
              @click="showProtoFileDemo"
              >View an example of a proto file</el-link
            >
            <el-upload
              class="upload-demo"
              drag
              multiple
              accept=".proto"
              action="#"
              :auto-upload="false"
              :on-change="handleOnchange"
              :on-remove="handleOnRemove"
              :file-list="fileList"
            >
              <i class="el-icon-upload"></i>
              <div class="el-upload__text">
                <em>click to select the proto file</em><br />or drag the file
                here, multi-file support
              </div>
              <!-- <div class="el-upload__tip" slot="tip">
                Only proto files can be uploaded
              </div> -->
            </el-upload>
          </el-form-item>

          <el-form-item
            label="service name"
            prop="serverName"
            style="margin-top: 50px;"
          >
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.serverName"
            ></el-input>
          </el-form-item>
          <el-form-item label="module name" prop="moduleName">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.moduleName"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                module name for go.mod file.
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="project name" prop="projectName">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.projectName"
            ></el-input>
          </el-form-item>
          <el-form-item label="docker repository address">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.repoAddr"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                Image repository address, the default value is
                image-repo-host,<br />
                this parameter is used to build docker images, k8s deployment<br />
                scripts, if you use the official docker image repository,
                just<br />
                fill in the user name of the registered docker repository, if<br />
                you use the private repository address, you need to fill in
                the<br />
                full repository address.
              </div>
            </el-tooltip>
          </el-form-item>
          <div style="margin: 30px auto;"></div>
        </el-form>

        <div class="conform-btns">
          <el-button size="default" @click="clearFormData">Reset</el-button>
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            >Download code</el-button
          >
          <DownloadCode ref="downloadCode"></DownloadCode>
        </div>
      </div>

      <el-dialog
        title="Example of a proto file"
        :visible.sync="showProtoDemoDialogVisible"
        width="66%"
        center
      >
        <div class="showProtoDemoDiv">
          <pre>{{ protoDemoContent }}</pre>
        </div>
      </el-dialog>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import DownloadCode from "@/components/en/DownloadCode";
export default {
  name: "WebHTTPPb",
  data() {
    return {
      title: ", generate generic web services project code",
      path: "web-http-pb",
      helpInfo: `
      <p><b>Web services created based on protobuf</b> includes the functionality needed from development to deployment. Simply define the api interface description information in the proto file and then write the specific logic code in the generated template code.</p>
      <br />
      <p>1. unzip the code file.</p>
      <p>2. open a terminal and execute commands to generate api interface template code, api interface error code, registration route code, swagger document, *pb.go file: <b style="color: green">make proto</b></p>
      <p>3. open file <span style="color: chocolate">internal/handler/xxx_logic.go</span>, replace <span style="color: chocolate">panic("implement me")</span> according to template code example.</p>
      <p>4. compile and run services:   <b style="color: green">make run</b></p>
      <p>5. Visit <a href="http://localhost:8080/apis/swagger/index.html" target="_blank">http://localhost:8080/apis/swagger/index.html</a> in your browser and test api interface.</p>
      `,

      labelPosition: "right",
      windowWidth: document.documentElement.clientWidth,
      spanSize: 16,
      offsetSize: 4,

      fileList: [],
      ruleForm: {
        moduleName: "",
        serverName: "",
        projectName: "",
        repoAddr: "",
        protobufFile: "",
      },
      rules: {
        moduleName: [
          {
            required: true,
            message: "please type the name of the go module",
            trigger: "blur",
          },
        ],
        serverName: [
          {
            required: true,
            message: "please type the name of the service",
            trigger: "blur",
          },
        ],
        projectName: [
          {
            required: true,
            message: "please type the project name",
            trigger: "blur",
          },
        ],
        protobufFile: [
          {
            required: true,
            message: "please select the proto file",
            trigger: "change",
          },
        ],
      },

      showProtoDemoDialogVisible: false,
      protoDemoContent: `syntax = "proto3";

package api.edusys.v1;

import "google/api/annotations.proto";
import "protoc-gen-openapiv2/options/annotations.proto";

option go_package = "edusys/api/user/v1;v1";

option (grpc.gateway.protoc_gen_openapiv2.options.openapiv2_swagger) = {
  host: "localhost:8080"
  base_path: ""
  info: {
    title: "user api docs";
    version: "2.0";
  };
  schemes: HTTP;
  schemes: HTTPS;
  consumes: "application/json";
  produces: "application/json";
};

service user {
  rpc Login(LoginRequest) returns (LoginReply) {
    option (google.api.http) = {
      post: "/api/v1/login"
      body: "*"
    };
    option (grpc.gateway.protoc_gen_openapiv2.options.openapiv2_operation) = {
      summary: "login",
      description: "login with email",
    };
  }
}

message LoginRequest {
  string email = 1;
  string password = 2;
}

message LoginReply {
  string token = 1;
}`,
    };
  },
  created: function() {
    this.adaptiveWindows();

    const apiUrl = appConfig.spongeServiceAddr + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;
        if (data.code == 0) {
          this.ruleForm.moduleName = data.data.moduleName;
          this.ruleForm.serverName = data.data.serverName;
          this.ruleForm.projectName = data.data.projectName;
          this.ruleForm.repoAddr = data.data.repoAddr;
          this.ruleForm.protobufFile = "";
        }
      })
      .catch(err => {
        // console.log(err);
      });
  },

  components: { DownloadCode },

  mounted() {
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.adaptiveWindows();
        });
      })();
    };
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let fd = new FormData();
          this.fileList.forEach(item => {
            fd.append("files", item.raw);
          });
          const uploadURL = appConfig.spongeServiceAddr + "/uploadFiles";
          axios
            .post(uploadURL, fd)
            .then(res => {
              if (res.data.code == 0) {
                const apiUrl = appConfig.spongeServiceAddr + "/generate";
                const arg = `web http-pb --module-name=${this.ruleForm.moduleName} --server-name=${this.ruleForm.serverName} --project-name=${this.ruleForm.projectName} --repo-addr=${this.ruleForm.repoAddr} --protobuf-file=${res.data.data}`;
                this.$refs.downloadCode.execution(
                  apiUrl,
                  this.path,
                  arg,
                  this.helpInfo,
                );
              } else {
                this.$message.error(res.data.msg);
              }
            })
            .catch(err => {
              this.$message.error("failed to download code");
            });
        } else {
          return false;
        }
      });
    },
    clearFormData() {
      this.ruleForm = { dsnAddr: this.ruleForm.dsnAddr };
    },
    handleOnchange(file, fileList) {
      this.ruleForm.protobufFile = file.name;
      this.fileList = fileList;

      if (fileList.length !== 0) {
        this.$refs.ruleForm.validateField("protobufFile");
      }
    },
    handleOnRemove(file, fileList) {
      if (fileList.length === 0) {
        this.ruleForm.protobufFile = "";
        this.$refs.ruleForm.validateField("protobufFile");
      }
    },

    showProtoFileDemo() {
      this.showProtoDemoDialogVisible = true;
    },

    adaptiveWindows() {
      this.windowWidth = document.documentElement.clientWidth;
      if (this.windowWidth < 900) {
        this.spanSize = 24;
        this.offsetSize = 0;
      } else {
        this.spanSize = 16;
        this.offsetSize = 4;
      }
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 25px !important;
}

.form-style {
  border: 2px solid #ddd;
  box-shadow: 0px 0px 10px #ccc;
  border-radius: 10px;
  padding: 40px 40px;
}

.form-style /deep/ .el-form-item__label {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  color: #111;
}

.upload-demo /deep/ .el-upload-dragger {
  height: 60%;
  width: 150%;
}

.el-upload-dragger .el-icon-upload {
  margin-top: 20px;
}
.el-upload__text {
  line-height: 1.5;
  margin-bottom: 20px;
}

.showProtoDemoDiv {
  width: 85%;
  margin: auto;
  font-size: 16px;
  color: black;
  background: rgba(255, 255, 255, 0.5);
  line-height: 20px;
  border: 1px solid #ccc;
  padding: 5px 20px;
  margin-bottom: 20px;
}

.el-icon-question {
  font-size: 22px;
  vertical-align: middle;
  color: #dcdfe6;
}

.tooltip-content {
  padding: 5px;
  color: #c46600;
  font-size: 14px;
  line-height: 150%;
}
</style>



